.app{
    height: 100vh;
     
    .sections{
        width: 100%;
        height: calc(100vh - 70px);
        background-color: lightsalmon;
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        &::-webkit-scrollbar{
            display: none
        };

        > * {
            scroll-snap-align: start;
            width: 100vw;
            height: calc(100vh - 70px);
        }
    }
}
